body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: -webkit-linear-gradient(left, rgba(72, 171, 199, 1) 0%, rgba(60, 154, 180, 1) 44%, rgba(53, 133, 154, 1) 73%, rgba(50, 123, 143, 1) 86%);

  background: linear-gradient(to right, rgba(72, 171, 199, 1) 0%, rgba(60, 154, 180, 1) 44%, rgba(53, 133, 154, 1) 73%, rgba(50, 123, 143, 1) 86%);
}

*::-webkit-scrollbar {
  width: 0
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
img {
    border: 2px solid transparent;
    margin: 10px;
    padding: 10px;
}
.active {
    border: 2px solid green;
}
